.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-image: url('https://www.google.com/url?sa=i&url=https%3A%2F%2Ftenor.com%2Fview%2Fpedro-pedro-gif-7082946732033593935&psig=AOvVaw2C91DyVOnNZE65f8CTRyZM&ust=1722368067167000&source=images&cd=vfe&opi=89978449&ved=0CBAQjRxqFwoTCKCD8Zj_zIcDFQAAAAAdAAAAABAE');
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #2c2c2c;
}

.Emp-header {
  background-image: url('data:image/jpeg;base64,');
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #2c2c2c;
}

.Not-header {
  background-image: url('data:image/jpeg;base64,');
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
}

.App-link {
  color: #e215ed;
}
#Button{
  padding: 10%;
}


@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg) scale(1);
  }
  5% {
    transform: rotate(0deg) scale(5);
  }
   25% {
    transform: rotate(0deg) scale(1);
  } 
   26% {
    transform: rotate(0deg) scale(1);
  }
  31% {
    transform: rotate(360deg) scale(5);
  }
  50% {
    transform: rotate(0deg) scale(1);
  }
  51% {
    transform: rotate(0deg) scale(1);
  }
  56% {
    transform: rotate(0deg) scale(5);
  }
  75% {
    transform: rotate(0deg) scale(1);
  } 
  76% {
    transform: rotate(0deg) scale(1);
  }
  81% {
    transform: rotate(360deg) scale(5);
  }
  100% {
    transform: rotate(0deg) scale(1);
  } 
}
